import { ibasketTotals } from 'src/app/common/interfaces/ibasket';

@Component({
  selector: 'basket-total',
  templateUrl: './basket-total.component.html'
})
export class BasketTotalComponent implements OnInit {

  $basketTotal:Observable<ibasketTotals>;

  constructor(private servBasket:BasketService) {
  }

  ngOnInit() {
    this.$basketTotal = this.servBasket.$basketTotal;
  }

}